import React, { useContext, useEffect, useState } from 'react';
import { GlobalContext } from '../context/GlobalState';
import Booth from './Booth';

export default function Booths() {
    const {centre, booths, updateVo} = useContext(GlobalContext);
    const [showBooths, setShowBooths] = useState([]);

    async function getFile() {
        await fetch("/submit", {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json'
          },
          body: JSON.stringify({
            centre: centre,
            booths: booths
          })
        })
          .then((res) => res.json())
          .then((data) => {
                window.open(`/download/${data.file}`, '_blank');
          });
    }

    useEffect(() => {
        var cur = [];
        for (let i = 0; i < centre.vocount; i++) {
            cur.push(<Booth 
                key={i} 
                number={i + 1}
                handleUpdate={handleUpdate} />);
        }
        setShowBooths(cur);
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [centre]);

    function handleUpdate(data) {
        updateVo(data);
    }
    
    return (
        <div>
            {showBooths}
            <button 
                className="btn btn-success shadow-lg mb-3 float-end"
                onClick={getFile}
            >Get Report</button>
        </div>
    );
}